import React, { useEffect } from 'react';
import '../../styles/App.scss';
import '../../styles/toolsCategories.scss'
import imageDrujbe from '../../images/drujbe.png';
import imageMotocoase from '../../images/motocoase.png';
import imageGenerators from '../../images/generator.png';
import HeaderCatalog from '../../UI modules/parts/headerCatalog/HeaderCatalog';
import { Link } from 'react-router';

function GasolineTools(props) {

    useEffect(() => {
        window.scrollTo(0, 0); //     
    }, []); //        

    const subcategories = [
        { image: imageDrujbe, link: '/gasolineTools/Chainsaws', name: 'Drujbe', description: 'Include fierstraie performante. Ideale pentru tierea lemnului n gospodrie i lucrri profesionale.' },
        { image: imageMotocoase, link: '/gasolineTools/BrushCutters', name: 'Motocoase', description: 'Include motocoase performante. Perfecte pentru lucrri de grdinrit i ntreinerea terenurilor.' },
        { image: imageGenerators, link: '/gasolineTools/Generators', name: 'Generatoare', description: 'Include generatoare. Potrivite pentru antiere i zone fr acces la reea.' },
    ];

    return (
        <div className='toolsCategories'>
            <HeaderCatalog />
            <div className='container' style={{ width: '100%' }}>
                <div className='sectionTitleContainer toolsCategoriesTitle'>
                    <div className='titleSecondary'>
                        <h2>Unelte Noastre Pe Benzin</h2>
                    </div>
                    <div className='titleDescrition'>
                        <p>
                            Explorai gama noastr de unelte pe benzin, de la drujbe robuste i motocoase versatile la generatoare fiabile, echipate cu motoare puternice, ideale pentru profesioniti i pasionai, oferind eficien i autonomie fr priz.
                        </p>
                    </div>
                </div>
                <div className='toolsCategoriesContainer'>
                    {subcategories.map((subcategory, index) => (
                        <div className='toolsCategoriesProduct' key={index}>
                            <Link to={subcategory.link} className='toolsCategoriesProductItem'>
                                <div className='toolsCategoriesProductImage'>
                                    <img src={subcategory.image} alt={subcategory.name}/>
                                </div>
                                <div className='cardTitleSecondary'>
                                    <h3>{subcategory.name}</h3>
                                </div>
                                <div className='cardDescriptionSecondary'>
                                    <p>{subcategory.description}</p>
                                </div>
                            </Link>
                        </div>
                    ))}
                </div>
            </div>
        </div>
    );
}

export default GasolineTools;