import React, { useEffect } from 'react';
import '../../styles/App.scss';
import productTaiere from '../../images/treeWork.png';
import productGaurie from '../../images/shurik.png';
import productSlefuire from '../../images/shlifovka.png';
import productFrezers from '../../images/frezers.png';
import productStroitlmie from '../../images/stroitelnie.png';
import HeaderCatalog from '../../UI modules/parts/headerCatalog/HeaderCatalog';
import { Link } from 'react-router';

function ElectroTools(props) {

    useEffect(() => {
        window.scrollTo(0, 0); //     
    }, []); //        

    const subcategories = [
        { image: productTaiere, link: '/electroTools/CuttingTools', name: 'Unelte pentru tiere i debitare', description: 'Include unelte. Utilizate pentru tiere i prelucrarea materialelor n ateliere i pe antier.' },
        { image: productGaurie, link: '/electroTools/DrillingAndFastening', name: 'Unelte pentru gurire i nurubare', description: 'Include unelte. Ideal pentru gurire precis i nurubare n proiecte casnice i industriale.' },
        { image: productSlefuire, link: '/electroTools/GrindingAndPolishing', name: 'Unelte pentru lefuire i lustruire', description: 'Include unelte. Perfecte pentru lefuire i lustruire n renovri i decor.' },
        { image: productFrezers, link: '/electroTools/WoodWorking', name: 'Unelte pentru prelucrarea lemnului i a altor materiale', description: 'Include unelte. Potrivite pentru lucrri de tmplrie i prelucrarea diverselor materiale.' },
        { image: productStroitlmie, link: '/electroTools/ConstructionAndFinishing', name: 'Unelte pentru lucrri de construcii i finisaje', description: 'Include unelte. Utilizate pentru construcii i finisaje interioare.' },
    ];

    return (
        <div className='toolsCategories'>
            <HeaderCatalog />
            <div className='container'>
                <div className='sectionTitleContainer toolsCategoriesTitle'>
                    <div className='titleSecondary'>
                        <h2>Unelte Noastre Electrice</h2>
                    </div>
                    <div className='titleDescrition'>
                        <p>
                            Descoper gama noastr de unelte electrice eficiente, perfecte pentru diverse lucrri profesionale i casnice.
                        </p>
                    </div>
                </div>
                <div className='toolsCategoriesContainer'>
                    {subcategories.map((subcategory, index) => (
                        <div className='toolsCategoriesProduct' key={index}>
                            <Link to={subcategory.link} className='toolsCategoriesProductItem'>
                                <div className='toolsCategoriesProductImage'>
                                    <img src={subcategory.image} alt={subcategory.name}/>
                                </div>
                                <div className='cardTitleSecondary'>
                                    <h3>{subcategory.name}</h3>
                                </div>
                                <div className='cardDescriptionSecondary'>
                                    <p>{subcategory.description}</p>
                                </div>
                            </Link>
                        </div>
                    ))}
                </div>
            </div>
        </div>
    );
}

export default ElectroTools;